/* roboto-regular - cyrillic_latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto'), local('Roboto-Regular'), url('../fonts/roboto-v20-cyrillic_latin-regular.woff2') format('woff2'), url('../fonts/roboto-v20-cyrillic_latin-regular.woff') format('woff');
}
/* roboto-700 - cyrillic_latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local('Roboto Bold'), local('Roboto-Bold'), url('../fonts/roboto-v20-cyrillic_latin-700.woff2') format('woff2'), url('../fonts/roboto-v20-cyrillic_latin-700.woff') format('woff');
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: 400;
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  position: relative;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
.button {
  min-width: 200px;
  padding: 12px 30px 12px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  background-color: #558cb7;
  border: none;
  border-radius: 3px;
}
.button:hover {
  cursor: pointer;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.4);
}
.button:focus {
  outline: none;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4);
}
.button:disabled {
  background-color: #82b3d9;
}
.button:disabled:hover {
  cursor: default;
  box-shadow: none;
}
.registration {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: #e5e5e5;
}
.registration__user-data {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 480px;
  min-height: 700px;
  margin-top: -350px;
  margin-left: -240px;
  background-color: #ffffff;
}
.registration__button:disabled {
  background-color: #81c784;
}
.user-data {
  padding: 41px 41px 40px 39px;
  margin-top: -2000px;
  color: #000000;
  opacity: 0;
  transition: margin-top 0.5s cubic-bezier(0, 0, 0.2, 1), opacity 0.2s 0.1s;
}
.show {
  margin-top: -350px;
  opacity: 1;
}
.user-data__title {
  margin: 0;
  margin-bottom: 29px;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
}
.user-data__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.user-data__item {
  position: relative;
}
.user-data__item:not(:last-child) {
  margin-bottom: 10px;
}
.user-data__item:last-child {
  margin-bottom: 18px;
}
.user-data__item label {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}
.user-data__item input:not([type='checkbox']) {
  height: 40px;
  padding: 0 15px;
  margin-top: 9px;
  border: 1px solid #dddddd;
}
.user-data__item input:focus {
  outline: none;
  border-color: #82b3d9;
}
.user-data__item p {
  min-height: 15px;
  margin: 0;
  font-size: 12px;
  line-height: 14px;
  color: #9f2b11;
}
.user-data__validation {
  margin-bottom: 7px;
}
.user-data__validation-title {
  margin: 0;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  color: #777777;
}
.user-data__validation-list {
  margin: 0;
  padding: 0;
  padding-left: 1px;
  list-style: none;
}
.user-data__validation-item {
  position: relative;
  padding-left: 19px;
  font-size: 12px;
  line-height: 14px;
  color: #777777;
}
.user-data__validation-item:not(:last-child) {
  margin-bottom: 5px;
}
.user-data__validation-item::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 14px;
  height: 14px;
  margin-top: -7px;
  border: 1px solid #777777;
  border-radius: 50%;
}
.user-data__validation-item--success {
  color: #007b1b;
}
.user-data__validation-item--success::before {
  border-color: #007b1b;
}
.user-data__validation-item--success::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 2px;
  width: 10px;
  height: 9px;
  background-image: url('../img/success.svg');
}
.user-data__validation-item--error {
  color: #9f2b11;
}
.user-data__validation-item--error::before {
  border-color: #9f2b11;
}
.user-data__validation-item--error::after {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 10px;
  height: 9px;
  background-image: url('../img/error.svg');
}
.user-data__agreement {
  margin: 0;
  margin-bottom: 41px;
  padding-left: 2px;
  font-size: 12px;
  line-height: 14px;
}
.user-data__agreement label {
  position: relative;
  padding-left: 18px;
}
.user-data__agreement label::before {
  content: '';
  position: absolute;
  left: 0;
  top: 1px;
  width: 12px;
  height: 12px;
  border: 1px solid #dddddd;
  border-radius: 3px;
}
.user-data__agreement-input + label:hover {
  cursor: pointer;
}
.user-data__agreement-input:focus + label::before {
  border-color: #558cb7;
}
.user-data__agreement-input:checked + label::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 1px;
  width: 10px;
  height: 9px;
  background-image: url('../img/checked.svg');
}
.user-data__agreement-input:checked + label::before {
  background-color: #558cb7;
  border-color: #558cb7;
}
.user-data__agreement-link {
  color: #558cb7;
}
.user-data__agreement-link:focus {
  outline: none;
  text-decoration: underline;
}
.user-data__button {
  display: block;
  margin: 0 auto;
}
.user-data__close {
  position: absolute;
  top: 38px;
  right: 47px;
  width: 20px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  border: none;
}
.user-data__close::before,
.user-data__close::after {
  content: '';
  position: absolute;
  top: 50%;
  left: -2px;
  width: 24px;
  height: 3px;
  margin-top: -2px;
  border-radius: 1px;
  background-color: #c4c4c4;
}
.user-data__close::before {
  transform: rotate(45deg);
}
.user-data__close::after {
  transform: rotate(-45deg);
}
.user-data__close:hover {
  cursor: pointer;
}
.user-data__close:hover::before,
.user-data__close:hover::after {
  background-color: #558cb7;
}
